import service from '../../untils/service.js'
export default {
	data() {
		return {
			swiper:[],
			loading:false,
			indicatorDots: true,
			autoplay: true,
			interval: 4000,
			duration: 300
		};
	},
	onLoad() {
		this.getBanner()
	},
	methods: {
		getBanner(){
		  uni.showLoading({
				    title: '加载中...'
			});
			uni.request({
				    url: service.getBanner, //仅为示例，并非真实接口地址。
				    data: {},
					method:'GET',
				    success: (res) => {
						console.log(res.data);
						this.swiper = res.data.data.imgList;
				    },
					fail:(err) => {
						uni.showToast({
						    title: '网络请求错误',
							icon:'none',
						    duration: 2000
						});
					},
					complete:() =>{
						this.loading = false;
						uni.hideLoading()
					}
			})
		}
	}
};
